import { faSort } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import GoogleMapReact from "google-map-react";
import React, { PureComponent } from "react";
import RotatedTitle from "../../assets/img/rotatedTitle.png";
import stateCoordinates from "../../data/states";
import StateInfoBox from "../usStatus/stateInfoBox/stateInfoBox";
import StateMarker from "../usStatus/stateMarker/stateMarker";
import classes from "./usStatus.module.css";

class usStatus extends PureComponent {
  retrieveData() {
    fetch("https://disease.sh/v3/covid-19/states")
      .then((blob) => blob.json())
      .then((data) =>
        this.setState({
          mainData: data.map((usState) => {
            return {
              ...usState,
              latitude:
                stateCoordinates.filter(
                  (coord) => coord.name === usState.state
                )[0] !== undefined
                  ? stateCoordinates.filter(
                      (coord) => coord.name === usState.state
                    )[0].lat
                  : 37.0902,
              longitude:
                stateCoordinates.filter(
                  (coord) => coord.name === usState.state
                )[0] !== undefined
                  ? stateCoordinates.filter(
                      (coord) => coord.name === usState.state
                    )[0].lng
                  : -95.7129,
            };
          }),
        })
      )
      .then(() => this.sortData());
  }

  sortData() {
    const sortedCase = [...this.state.mainData]
      .sort((a, b) => b.cases - a.cases)
      .slice(0, 20);
    const sortedDeaths = [...this.state.mainData]
      .sort((a, b) => b.deaths - a.deaths)
      .slice(0, 20);
    const sortedTodayCases = [...this.state.mainData]
      .sort((a, b) => b.todayCases - a.todayCases)
      .slice(0, 20);
    const sortedTodayDeaths = [...this.state.mainData]
      .sort((a, b) => b.todayDeaths - a.todayDeaths)
      .slice(0, 20);
    const sortedCasesPerOneMillion = [...this.state.mainData]
      .sort((a, b) => b.casesPerOneMillion - a.casesPerOneMillion)
      .slice(0, 20);
    const sortedDeathsPerOneMillion = [...this.state.mainData]
      .sort((a, b) => b.deathsPerOneMillion - a.deathsPerOneMillion)
      .slice(0, 20);

    this.setState({
      top20Cases: {
        cases: sortedCase,
        deaths: sortedDeaths,
        todayCases: sortedTodayCases,
        todayDeaths: sortedTodayDeaths,
        casesPerOneMillion: sortedCasesPerOneMillion,
        deathsPerOneMillion: sortedDeathsPerOneMillion,
      },
    });
  }

  handleWindowResize() {
    window.addEventListener("resize", (e) => {
      if (e.srcElement.outerWidth >= 750) {
        this.setState({ isSmallScreen: false, mapHeight: 60 });
      } else if (e.srcElement.outerWidth < 750) {
        this.setState({ isSmallScreen: true, mapHeight: 100 });
      }
    });
  }

  handleFirstTable() {
    if (window.outerWidth >= 750) {
      this.setState({ isSmallScreen: false, mapHeight: 60 });
    } else if (window.outerWidth < 750) {
      this.setState({ isSmallScreen: true, mapHeight: 100 });
    }
  }

  constructor(props) {
    super(props);
    this.state = {
      mainData: [],
      top20Cases: {
        cases: [],
        todayCases: [],
        deaths: [],
        todayDeaths: [],
        casesPerOneMillion: [],
        deathsPerOneMillion: [],
      },
      sortBy: "cases",
      selectedState: null,
      isSmallScreen: false,
      mapHeight: 60,
    };
  }

  componentDidMount() {
    this.retrieveData();
    this.handleFirstTable();
    this.handleWindowResize();
  }

  render() {
    const values = 
    this.state.mainData
      .filter((usState) => {
        return stateCoordinates
          .map((state) => state.name)
          .includes(usState.state);
      }).map((usState) => usState[this.state.sortBy]);

      const minValue = Math.min(...values);
      const maxValue = Math.max(...values);

    return (
      <div>
        <div
          onClick={() => this.setState({ selectedState: null })}
          style={{ height: `${this.state.mapHeight}vh`, width: "100%" }}
        >
          <GoogleMapReact
            bootstrapURLKeys={{
              key: "AIzaSyA3jSaFgByAz1ZNwNWJXj_HmoEMntLPEj8",
              language: "en",
            }}
            defaultCenter={[37.09, -95.71]}
            defaultZoom={4}
          >
            {this.state.mainData
              .filter((usState) => {
                return stateCoordinates
                  .map((state) => state.name)
                  .includes(usState.state);
              })
              .map((usState) => {
                return (
                  <StateMarker
                    data={usState}
                    key={usState.state}
                    lat={usState.latitude}
                    lng={usState.longitude}
                    max={maxValue}
                    min={minValue}
                    onClick={(event) => {
                      this.setState({ selectedState: usState });
                      event.stopPropagation();
                    }}
                    sortBy={this.state.sortBy}
                  />
                );
              })}

            {this.state.selectedState ? (
              <StateInfoBox
                lat={this.state.selectedState.latitude}
                lng={this.state.selectedState.longitude}
                stateData={this.state.selectedState}
              />
            ) : null}
          </GoogleMapReact>
        </div>
        {this.state.isSmallScreen ? null : (
          <div className={classes.table__container}>
            <p className={classes.table__info}>
              {" "}
              You can click on the headers to see data ordered for each
              parameter.
            </p>
            <div className={classes.table__inner__container}>
              <div className={classes.table__header__container}>
                <img
                  alt="table title"
                  className={classes.table__header}
                  src={RotatedTitle}
                />
              </div>
              <table className={classes.table}>
                <thead>
                  <tr>
                    <th>State</th>
                    <th
                      className={
                        this.state.sortBy === "cases"
                          ? classes.active_case
                          : null
                      }
                      onClick={() =>
                        this.setState({
                          sortBy: "cases",
                        })
                      }
                    >
                      Cases <FontAwesomeIcon icon={faSort} />
                    </th>
                    <th
                      className={
                        this.state.sortBy === "todayCases"
                          ? classes.active_case
                          : null
                      }
                      onClick={() => this.setState({ sortBy: "todayCases" })}
                    >
                      Today&apos;s Cases <FontAwesomeIcon icon={faSort} />
                    </th>
                    <th
                      className={
                        this.state.sortBy === "deaths"
                          ? classes.active_death
                          : null
                      }
                      onClick={() => this.setState({ sortBy: "deaths" })}
                    >
                      Deaths <FontAwesomeIcon icon={faSort} />
                    </th>
                    <th
                      className={
                        this.state.sortBy === "todayDeaths"
                          ? classes.active_death
                          : null
                      }
                      onClick={() => this.setState({ sortBy: "todayDeaths" })}
                    >
                      Today&apos;s Deaths <FontAwesomeIcon icon={faSort} />
                    </th>
                    <th
                      className={
                        this.state.sortBy === "casesPerOneMillion"
                          ? classes.active_casesPerMillion
                          : null
                      }
                      onClick={() =>
                        this.setState({ sortBy: "casesPerOneMillion" })
                      }
                    >
                      Cases per 1M <FontAwesomeIcon icon={faSort} />
                    </th>
                    <th
                      className={
                        this.state.sortBy === "deathsPerOneMillion"
                          ? classes.active_deathsPerMillion
                          : null
                      }
                      onClick={() =>
                        this.setState({ sortBy: "deathsPerOneMillion" })
                      }
                    >
                      Deaths per 1M <FontAwesomeIcon icon={faSort} />
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {this.state.top20Cases[this.state.sortBy].map((usState) => {
                    return (
                      <tr key={usState.state}>
                        <td>{usState.state}</td>
                        <td>{usState.cases.toLocaleString()}</td>
                        <td>{usState.todayCases.toLocaleString()}</td>
                        <td>{usState.deaths.toLocaleString()}</td>
                        <td>{usState.todayDeaths.toLocaleString()}</td>
                        <td>{usState.casesPerOneMillion.toLocaleString()}</td>
                        <td>{usState.deathsPerOneMillion.toLocaleString()}</td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          </div>
        )}
      </div>
    );
  }
}

export default usStatus;
